/*
  Time complexity: O(n)
  Space complexity: O(n)
*/
class ListNode {
    /* ------------------------------------------ Structure ------------------------------------------ */
    public val: number;
    public next: ListNode | null;

    constructor(val: number, next: ListNode | null = null) {
        this.val = val;
        this.next = next;
    }

    /* ------------------------------------------ Create ------------------------------------------ */
      /* 
      üí°Concept
