        2. Ë≥¶ÂÄº
        3. ÂõûÂÇ≥ dummy.next
      */
    public static create(values: number[]): ListNode | null {
        let dummy = new ListNode(0); // ËôõÊì¨È†≠ÁØÄÈªû
        let current = dummy;
        for (let val of values) {
            current.next = new ListNode(val);
            current = current.next;
        }

        /* FP ÂØ´Ê≥ï
        values.forEach((val) => {
            current.next = new ListNode(val);
            current = current.next;
        });
        */

        return dummy.next; // ËøîÂõûÁúüÊ≠£ÁöÑÈ†≠ÁØÄÈªû
    }

    /* ------------------------------------------ Print ------------------------------------------ */
    public static print(head: ListNode | null): void {
        let current = head;
        let result: number[] = [];
        while (current !== null) {
            result.push(current.val);
            current = current.next;
        }
        console.log(result.join(" -> "));
    }

    /* FP ÂØ´Ê≥ï
