    /* ------------------------------------------ Leetcode_2 ------------------------------------------ */
    public static addTwoNumbers(l1: ListNode | null, l2: ListNode | null): ListNode | null {
        let dummy = new ListNode(0); // ËôõÊì¨È†≠ÁØÄÈªû
        let current = dummy;
        let carry = 0; // ÈÄ≤‰ΩçÊï∏ÂÄº

        while (l1 !== null || l2 !== null || carry > 0) {
            let sum = carry;

            if (l1 !== null) {
                sum += l1.val;
                l1 = l1.next;
            }

            if (l2 !== null) {
                sum += l2.val;
                l2 = l2.next;
            }

            carry = Math.floor(sum / 10);
            current.next = new ListNode(sum % 10);
            current = current.next;
        }

        return dummy.next;
    }

    /* ------------------------------------------ Insertion (ÊèíÂÖ•) ------------------------------------------ */
    public static insert(head: ListNode | null, index: number, val: number): ListNode | null {
        let dummy = new ListNode(0, head);
        let current = dummy;
        let count = 0;

        while (current !== null && count < index) {
            current = current.next!;
            count++;
        }

        if (current !== null) {
            let newNode = new ListNode(val, current.next);
            current.next = newNode;
        }

        return dummy.next;
    }

    /* ------------------------------------------ Deletion (Âà™Èô§) ------------------------------------------ */
    public static delete(head: ListNode | null, index: number): ListNode | null {
        let dummy = new ListNode(0, head);
        let current = dummy;
        let count = 0;

        while (current.next !== null && count < index) {
            current = current.next;
            count++;
        }

        if (current.next !== null) {
            current.next = current.next.next;
        }

        return dummy.next;
    }

    /* ------------------------------------------ Update (Êõ¥Êñ∞) ------------------------------------------ */
    public static update(head: ListNode | null, index: number, newVal: number): ListNode | null {
        let current = head;
        let count = 0;

        while (current !== null && count < index) {
            current = current.next;
            count++;
        }

        if (current !== null) {
            current.val = newVal;
        }

        return head;
    }

    /* ------------------------------------------ Search (ÊêúÂ∞ã) ------------------------------------------ */
    public static search(head: ListNode | null, val: number): number {
        let current = head;
        let index = 0;

        while (current !== null) {
            if (current.val === val) {
                return index;
            }
            current = current.next;
            index++;
        }

        return -1; // Ëã•Êâæ‰∏çÂà∞ÂâáÂõûÂÇ≥ -1
    }
}
